<template>
	
	<view class="wrapper" >
		<image src="http://qp9mr1fhs.hn-bkt.clouddn.com/img/sp-bg.jpg" class="bg"></image>
		<view class="ytitle">
			<text class="ytext">商品操作</text>
		</view>
		<!-- 轮播图 -->
		<view class="whitebox">		
					<image :src="list.img" class="spimg"></image>
		</view>
		<connect></connect>
		<view class="whitebox">
			<view class="utitlebox" style="margin-top: 20upx;margin-bottom: 10upx;">
			    <view class="utitleblock"></view>
			    <view class="utitle">商品信息</view>
			</view>
			<u-form style="margin-left: 20upx;">
					<u-form-item label="商品名称:"><text>{{list.name}}</text><text style="margin-left: 10upx;">（{{list.gg}}）</text></u-form-item>
					<u-form-item label="生源产地:"><text>{{list.cd}}</text></u-form-item>
					<u-form-item label="生产日期:"><text>{{list.scday}}</text></u-form-item>
					<u-form-item label="批次编号:"><text>{{list.pcbh}}</text></u-form-item>
					<u-form-item label="入库时间:" :class="list.rktime == '' ? 'none':'' "><text>{{list.rktime}}</text></u-form-item>
					<u-form-item label="操作员工:" :class="list.rkry == '' ? 'none':'' "><text>{{list.rkry}}</text><text style="margin-left: 10upx;">{{list.rknum}}</text></u-form-item>
					<u-form-item label="出库时间:" :class="list.cktime == '' ? 'none':'' "><text>{{list.rktime}}</text></u-form-item>
			</u-form>
		</view>
		<view class="btbox">
				<view class="bt1" @click="btback">返回</view>
				<view  @click="btconfirmrk" :class="crzt == 1 ? 'bt2':'none'">入库</view>
				<view  @click="btconfirmck" :class="crzt == 2  ? 'bt2':'none'">出库</view>
				<view  :class="crzt == 0  ? 'bt3':'none'">已出库</view>
		</view>
	</view>	
</template>

<script>
	export default {
		data() {
			return {
				list:{
					img: 'http://qp9mr1fhs.hn-bkt.clouddn.com/img/xiangjiao.jpg',
					name:'云南高山香蕉',
					gg:'500g',
					cd:'广东省中山市古镇镇香蕉乐园',
					scday:'2020-12-15',
					pcbh:'35451184318',
					rktime:'',
					rkry:'',
					rknum:'',
					cktime:''
				},
				crzt:1,
				ck:false
			}			
		},
		methods:{
			btback(){
				uni.navigateTo({
					url:'operation-list'
				})
			},
			btconfirmrk(){
				uni.showModal({
					title:'提示',
					content: "您是否对该商品进行入库处理",
					confirmColor:'#0ABB9A',
					success: (res) => {
						if (res.confirm) {
							uni.showToast({
								title:'入库成功'
							})
							let myDate = new Date()
							let year = myDate.getFullYear()
							let month = myDate.getMonth()+1
							let date = myDate.getDate()
							let hours = myDate.getHours()
							let minutes = myDate.getMinutes()
							let seconds = myDate.getSeconds()
							if(minutes<10 || hours<10 || seconds<10){
								minutes = '0'+minutes
							}
							this.crzt =2;
							this.list.rktime = year+'-'+month+'-'+date+' '+hours+':'+minutes+':'+seconds
							this.list.rkry = '吴彦祖'
							this.list.rknum = '(1550785454)'
						} else if (res.cancel) {}
					}
				})
				
				
			},
			btconfirmck(){
				uni.showModal({
					title:'提示',
					content: "您是否对该商品进行出库处理",
					confirmColor:'#0ABB9A',
					success: (res) => {
						if (res.confirm) {
							uni.showToast({
								title:'出库成功'
							})
							let myDate = new Date()
							let year = myDate.getFullYear()
							let month = myDate.getMonth()+1
							let date = myDate.getDate()
							let hours = myDate.getHours()
							let minutes = myDate.getMinutes()
							let seconds = myDate.getSeconds()
							if(minutes<10 || hours<10 || seconds<10){
								minutes = '0'+minutes
							}
							this.ck = true
							this.crzt = 0
							this.list.cktime = year+'-'+month+'-'+date+' '+hours+':'+minutes+':'+seconds
						} else if (res.cancel) {}
					}
				})
				
				}
				
		},
		onLoad(res) {
			// console.log(res)
		}
}			
</script>

<style>
	page{
		background-color:transparent;
	}
	.bg{
		position: absolute;
		z-index: -1;
		
		left: 0;
		width: 100%;
		height: 1300upx;
		
	}
	.whitebox:first-child{
		padding-bottom: 60upx;
	}
	.spimg{
		width: 570upx;
		height:380upx ;
		margin-top: 20upx;
		border-radius: 30upx;
		box-shadow: 0 0 5upx #C0C4CC;
		margin-left: 20upx;
		margin-bottom: 20upx;
	}
	.btbox{
		padding-bottom: 80upx;
		}
	.none{
		display: none;
	}	
</style>

